.CreateTodoButton {
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 24px;
    position: fixed;
    bottom: 40px;
    right: 40px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 94px;
    width: 94px;

    transform: rotate(0);
    transition: .2s ease;

}

.CreateTodoButton:hover {
    transform: scale(1.7);
}

